// Header start ========================================================================================================================================================
body {
  background-color: var(--grey-color);
}

.site-container {
  background-color: var(--grey-color);
}
.title {
  color: var(--white-color);
}

.btn {
  color: var(--light-color);
  background-color: var(--green-dark);
}

.header {
  background-color: var(--grey-color);
  &.scroll {
    box-shadow: 0 0 10px 0px rgba(#00B871, 0.4);
}
  // .theme

  .nav {
    > ul {
      > li {
        > a {
          color: var(--white-color);
          @media (min-width: 769px){
            &:hover {
              color: var(--green-dark);
            }
            &:after {
              background-color: var(--green-dark);
            }
            &.active {
              color: var(--green-dark);
            }
          }
        }
      }
      @media (max-width: 768px){
        box-shadow: 0px 0px 16px 4px rgba(47, 223, 132, 0.15);
        border: 1px solid var(--green-light);
      }
    }
    @media (max-width: 768px){
        background-color: var(--grey-color);
    }
  }
  .burger {
    color: var(--white-color);
  }
}

// Header end ========================================================================================================================================================

// Hero section start ========================================================================================================================================================

.hero {
  // .hero__content

  &__content {
    h1 {
      span {
        color: var(--white-color)
      }
    }
  }

  // .hero__subtitle

  &__subtitle {
    color: var(--white-color);
  }
}

// Hero section end ========================================================================================================================================================

// Services section start ========================================================================================================================================================

.services {

  // .services__title

  &__title {
    color: var(--white-color);
  }

  // .services__subtitle

  &__subtitle {
    color: var(--white-color);
  }

  // .services__name

  &__name {
    color: var(--white-color);
  }

  // .services__text

  &__text {
    color: var(--white-color);
  }
}

// Services section end ========================================================================================================================================================

// Section how it's work start ========================================================================================================================================================

.how-its-work {
  background-color: var(--grey-color);

  // .how-its-work__subtitle

  &__subtitle {
    color: var(--white-color);
  }

  // .how-its-work__name

  &__name {
    color: var(--white-color);
  }

  // .how-its-work__text

  &__text {
    color: var(--white-color);
  }
}

// Section how it's work end========================================================================================================================================================

// Section price start ========================================================================================================================================================

.price {

  // .price__subtitle

  &__subtitle {
    color: var(--white-color);
  }

}

// Section price end ========================================================================================================================================================

// Section gallery start ========================================================================================================================================================


.gallery {
  background-color: var(--grey-color);
}

// Section gallery end ========================================================================================================================================================

// Section FAQ start ========================================================================================================================================================

.faq {
  background-color: var(--grey-color);

  // .faq__subtitle

  &__subtitle {
    color: var(--white-color);
  }

  // .faq__description

  &__description {
    color: var(--white-color);
    > a {
      color: var(--white-color);
    }
  }
}

.accordion {
  background: var(--grey-color);

  // .accordion__content

  &__content {
      border: 2px solid #333;
      background: var(--grey-color);
      &.open {
        .accordion__title {
          color: var(--green-dark);
      }
    }
  }

  // .accordion__header

  &__title {
    color: var(--white-color);
  }

  // .accordion__description

  &__description {
    color: var(--white-color);
  }
}

// Section FAQ end ========================================================================================================================================================

// Section contact-us start ========================================================================================================================================================

.contact {
  background-color: var(--grey-color);

  // .contact__descr

  &__descr {
    color: var(--white-color);
  }

  // .contact__address

  &__address {
    > ul {
      > li {
        color: var(--white-color);
        svg {
          path {
            fill: var(--white-color);
          }
        }
        > a {
          color: var(--white-color);
        }
      }
    }
  }

  // .contact__social

  &__social {
    > ul {
      > li {
        > a {
          svg {
            path {
              fill: var(--white-color);
            }
          }
        }
      }
    }
  }
}

// Section contact-us end ========================================================================================================================================================
